* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 4500px;
  background-image: url('./assets/mars.png');
  background-position: top;
  background-repeat: no-repeat;
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: linear-gradient(
    180deg,
    rgba(64, 5, 29, 0) 0%,
    rgba(52, 41, 30, 0.47102591036414565) 18%,
    rgba(0, 0, 0, 1) 22%
  );

  --border-color: 1px solid rgba(240, 235, 249, 0.5);
}

@font-face {
  font-family: 'Speedy';
  src: url('./fonts/SpeedyRegular-7BLoE.ttf');
}

@font-face {
  font-family: 'Typewriter';
  src: url('./fonts/1952Rheinmetall-85rB.ttf');
}

@font-face {
  font-family: 'StarShield';
  src: url('./fonts/StarShieldRegular-K7DjW.ttf');
}
